body {
  height: 100%;
  margin: 0;
  padding: 0;
}

#pokedex {
  display: grid;
  flex-direction: column;

  max-width: 100%;
  height: 100%;
  font-family: 'Segoe UI', sans-serif;

  background-color: #313131;
  background-image: url("https://i.imgur.com/rmgngIu.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  overflow: hidden;

  margin: 0;
}



h1 {
  font-size: 3rem;
  font-weight: 700;
  color: #f9f9f9;
}



.descricao {
  font-size: 22px;
  font-weight: 600;
  color: #f9f9f9;
}

.container-formulario {
  display: flex;
  justify-content: center;
  gap: 5px;
}

.pesquisa {
  outline: none;
  color: white;
  border: none;


  font-size: 18px;
  background-color: transparent;
  padding: 5px;

  position: absolute;

  left: 54%;
  top: 45%;

}


.btn:hover {
  opacity: 0.9;
  cursor: pointer;
  outline: 1px solid #ffffff;
}

.btn-pesquisar {
  background-color: transparent;
  display: grid;

  border: none;
  color: white;
  font-size: 13px;

  position: absolute;

  top: 70%;
  left: 53.6%;
}

.btn-limpar {
  background-color: transparent;

  border: none;
  color: white;
  font-size: 13px;

  position: absolute;

  top: 70%;
  left: 60.5%;
}

.card-pokemon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;

  max-width: 350px;
  height: 450px;

  background-color: #e1e1e1;
  color: #313131;

  border-radius: 1rem;

  margin-top: 5%;
}

.card-pokemon:hover {
  cursor: pointer;
  background-color: #3131317a;
  transform: rotate(360deg);
  /* Faz uma rotação completa (360 graus) */
  transform-origin: center;
  /* Define o ponto de origem da rotação como o centro do elemento */
  transition: transform 0.8s ease;
  /* Adiciona uma transição suave para a animação */
}

#telaPoke {

  width: 17vh;
  height: 13vh;

  outline: none;
  position: absolute;

  top: 42.5%;
  left: 38%;
  object-fit: contain;



  visibility: hidden;


}

#idPokemon {
  width: 10px;
  height: 10px;
  font-size: 20px;
  font-weight: bold;

  position: absolute;

  top: 65%;
  left: 39.5%;

}

.grid-pokemons {
  display: grid;
  /* grid-template-columns: repeat(10, 80px); */
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));

  column-gap: 40px;

  /* position: absolute; */
  bottom: 0;



}

.card-pokemon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-radius: 50%;

  height: 100px;
  width: 100px;

  background-color: #e1e1e15c;
  color: #313131;


  margin-top: 5%;
}

#lista {
  max-width: 100%;
  height: auto;
  background-color: #7FAC71;
}`, "",{"version":3,"sources":["webpack://./src/views/tela-inicio/tela-inicio.css"],"names":[],"mappings":"AAAA;;EAEE,YAAY;EACZ,SAAS;EACT,UAAU;AACZ;;AAEA;EACE,aAAa;EACb,sBAAsB;;EAEtB,eAAe;EACf,YAAY;EACZ,mCAAmC;;EAEnC,yBAAyB;EACzB,wDAAwD;EACxD,4BAA4B;EAC5B,2BAA2B;EAC3B,sBAAsB;;EAEtB,gBAAgB;;EAEhB,SAAS;AACX;;;;AAIA;EACE,eAAe;EACf,gBAAgB;EAChB,cAAc;AAChB;;;;AAIA;EACE,eAAe;EACf,gBAAgB;EAChB,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,QAAQ;AACV;;AAEA;EACE,aAAa;EACb,YAAY;EACZ,YAAY;;;EAGZ,eAAe;EACf,6BAA6B;EAC7B,YAAY;;EAEZ,kBAAkB;;EAElB,SAAS;EACT,QAAQ;;AAEV;;;AAGA;EACE,YAAY;EACZ,eAAe;EACf,0BAA0B;AAC5B;;AAEA;EACE,6BAA6B;EAC7B,aAAa;;EAEb,YAAY;EACZ,YAAY;EACZ,eAAe;;EAEf,kBAAkB;;EAElB,QAAQ;EACR,WAAW;AACb;;AAEA;EACE,6BAA6B;;EAE7B,YAAY;EACZ,YAAY;EACZ,eAAe;;EAEf,kBAAkB;;EAElB,QAAQ;EACR,WAAW;AACb;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,uBAAuB;EACvB,mBAAmB;EACnB,SAAS;;EAET,gBAAgB;EAChB,aAAa;;EAEb,yBAAyB;EACzB,cAAc;;EAEd,mBAAmB;;EAEnB,cAAc;AAChB;;AAEA;EACE,eAAe;EACf,2BAA2B;EAC3B,yBAAyB;EACzB,yCAAyC;EACzC,wBAAwB;EACxB,kEAAkE;EAClE,+BAA+B;EAC/B,iDAAiD;AACnD;;AAEA;;EAEE,WAAW;EACX,YAAY;;EAEZ,aAAa;EACb,kBAAkB;;EAElB,UAAU;EACV,SAAS;EACT,mBAAmB;;;;EAInB,kBAAkB;;;AAGpB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,eAAe;EACf,iBAAiB;;EAEjB,kBAAkB;;EAElB,QAAQ;EACR,WAAW;;AAEb;;AAEA;EACE,aAAa;EACb,6CAA6C;EAC7C,2DAA2D;;EAE3D,gBAAgB;;EAEhB,wBAAwB;EACxB,SAAS;;;;AAIX;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,uBAAuB;EACvB,mBAAmB;EACnB,SAAS;EACT,kBAAkB;;EAElB,aAAa;EACb,YAAY;;EAEZ,2BAA2B;EAC3B,cAAc;;;EAGd,cAAc;AAChB;;AAEA;EACE,eAAe;EACf,YAAY;EACZ,yBAAyB;AAC3B","sourcesContent":["html,\r\nbody {\r\n  height: 100%;\r\n  margin: 0;\r\n  padding: 0;\r\n}\r\n\r\n#pokedex {\r\n  display: grid;\r\n  flex-direction: column;\r\n\r\n  max-width: 100%;\r\n  height: 100%;\r\n  font-family: 'Segoe UI', sans-serif;\r\n\r\n  background-color: #313131;\r\n  background-image: url(\"https://i.imgur.com/rmgngIu.png\");\r\n  background-repeat: no-repeat;\r\n  background-position: center;\r\n  background-size: cover;\r\n\r\n  overflow: hidden;\r\n\r\n 