import { PokemonService } from '../../services/pokemon.service';
import './tela-inicio.css';
var TelaInicio = /** @class */ (function () {
    function TelaInicio() {
        var _this = this;
        this.pokemonService = new PokemonService();
        this.registrarElementos();
        this.registrarEventos();
        this.pokemonService.pesquisarListaPokemons()
            .then(function (pokemons) { return _this.gerarGridPokemons(pokemons); });
    }
    TelaInicio.prototype.gerarGridPokemons = function (pokemons) {
        var pnlGrid = document.createElement('div');
        pnlGrid.classList.add('grid-pokemons');
        for (var _i = 0, pokemons_1 = pokemons; _i < pokemons_1.length; _i++) {
            var pokemon = pokemons_1[_i];
            var card = this.obterCard(pokemon);
            pnlGrid.appendChild(card);
        }
        this.lista.appendChild(pnlGrid);
    };
    TelaInicio.prototype.obterCard = function (pokemon) {
        var _this = this;
        // const id = document.createElement("p");
        var imagem = document.createElement("img");
        // const nomePokemon = document.createElement("p");
        // id.textContent = pokemon.id.toString();
        // nomePokemon.textContent = pokemon.nome;
        imagem.src = pokemon.spriteUrl;
        var cardPokemon = document.createElement('div');
        cardPokemon.classList.add('card-pokemon');
        cardPokemon.addEventListener('click', function () { return _this.redirecionarUsuario(pokemon.nome); });
        // cardPokemon.appendChild(id);
        cardPokemon.appendChild(imagem);
        // cardPokemon.appendChild(nomePokemon);
        return cardPokemon;
    };
    TelaInicio.prototype.registrarElementos = function () {
        this.formPrincipal = document.getElementById("formPrincipal");
        this.txtPesquisa = document.getElementById("txtPesquisa");
        this.btnLimpar = document.getElementById("btnLimpar");
        this.pnlConteudo = document.getElementById("pnlConteudo");
        this.telaPoke = document.getElementById("telaPoke");
        this.idPokemon = document.getElementById("idPokemon");
        this.lista = document.getElementById('lista');
    };
    TelaInicio.prototype.registrarEventos = function () {
        var _this = this;
        this.formPrincipal.addEventListener("submit", function (e) { return _this.buscar(e); });
        this.btnLimpar.addEventListener('click', function () { return _this.limparCard(); });
    };
    TelaInicio.prototype.limparCard = function () {
        this.idPokemon.textContent = "";
        this.telaPoke.hidden = true;
        this.txtPesquisa.value = "";
    };
    TelaInicio.prototype.buscar = function (sender) {
        sender.preventDefault();
        var nome = this.txtPesquisa.value;
        this.pesquisarPokemon(nome);
    };
    TelaInicio.prototype.pesquisarPokemon = function (nome) {
        var _this = this;
        this.pokemonService.pesquisarPokemon(nome)
            .then(function (poke) { return _this.gerarCard(poke); })
            .then(function (poke) { return _this.redirecionarUsuario(nome); })
            .catch(function (err) { return _this.nomeInvalido(); });
    };
    TelaInicio.prototype.redirecionarUsuario = function (nome) {
        window.location.href = "pokemon-detalhes.html?nome=" + nome;
    };
    TelaInicio.prototype.gerarCard = function (pokemon) {
        this.telaPoke.style.visibility = 'visible';
        this.telaPoke.hidden = false;
        this.idPokemon.textContent = pokemon.id.toString();
        this.telaPoke.src = pokemon.spriteUrl;
        this.txtPesquisa.value = pokemon.nome.toUpperCase();
    };
    TelaInicio.prototype.nomeInvalido = function () {
        this.txtPesquisa.value = "Não encontrado!";
    };
    return TelaInicio;
}());
window.addEventListener('load', function () { return new TelaInicio(); });
